.admin-container {
  justify-content: center;
  padding: 20px;
  background-color: #f5f5f5;
  width: 100%;
  height: 100%;
  position: fixed;
  padding-top: 20;
  padding-bottom: 20;
}

.panelOff {
  z-index: -10;
  opacity: 0;
  /* transition: 1s; */
}

.panelOn {
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  opacity: 1;
  z-index: 0;
  /* transition: 0.3s; */
}

.panelOn > .admin-panel {
  right: 0;
  transition: 0.1s;
}

.admin-panel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 400px;
  height: 100%;
  position: relative;
  right: -100%;
}

.admin-title {
  text-align: center;
  margin-top: 10px;
  margin-bottom: 20px 0;
  color: #333;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-size: 14px;
  color: #555;
}

.form-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.btn-submit,
.btn-action,
.btn-company {
  background-color: #1890ff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
  width: 100%;
  border: 1px solid #1890ff;
}

.btn-submit:hover,
.btn-action:hover,
.btn-company:hover {
  background-color: #ffffff;
  transition: 0.2s;
  color: #1890ff;
  border: 1px solid #1890ff;
}

.button-group {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px; /* Расстояние между кнопками */
}

.btn-company {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

.btn-company {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

.btn-company.active {
  background-color: #ffffffd5; /* Пример цвета для активной кнопки */
  color: #1890ff;
  border: 2px solid #1890ff;
}

/* Размещение кнопок по строкам */
.button-group button:nth-child(4),
.button-group button:nth-child(5) {
  grid-column: span 1;
}

.error-message {
  color: red;
  font-size: 14px;
  margin-bottom: 10px;
}

.upload-form input[type="file"] {
  margin-top: 10px;
}
.OpenPanelAmin {
  position: fixed;
  right: 0;
  width: 15%;
  text-align: center;
  top: 0;
  z-index: 1;
}

.NonAdmin {
  display: none;
}

.honest-sign-form {
  display: flex;
