.firm-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.firm-list li {
  padding: 8px 0;
  border-bottom: 1px solid #eee;
  list-style: none;
}

.firm-list li {
  background: #f0f4f8; /* светлый фон для каждого элемента */
  border: 1px solid #d1e3f0;
  border-radius: 8px;
  margin: 8px 0;
  padding: 12px;
  cursor: pointer;
  transition: background 0.3s;
}

.firm-list li:hover {
  background: #e3efff; /* светло-синий при наведении */
}

.firm-list li > div {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  color: #2b6cb0; /* темно-синий текст */
}

.firm-list li > div span {
  color: #718096; /* серый цвет для количества */
  font-weight: normal;
}

/* Стили для вложенного списка моделей */
.model-list {
  list-style: none;
  padding: 8px 16px; /* отступ для иерархии */
  margin: 8px 0 0;
  background: #f9fbfc; /* фон для выделения моделей */
  border-radius: 6px;
  border-left: 3px solid #90cdf4; /* голубая полоса слева */
  transition: max-height 0.3s ease-in-out;
}

.model-list li {
  padding: 4px 0;
  font-size: 0.95em;
  color: #4a5568; /* темно-серый цвет текста */
  display: flex;
  justify-content: space-evenly;
}

/* Анимация раскрытия */
.firm-list li > .model-list {
  transition: 2s;
  max-height: 500px;
  overflow: auto;
}

.firm-list li.expanded > .model-list {
  max-height: 500px; /* максимальная высота для анимации */
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); /* Затемнение */
  display: flex;
  justify-content: center; /* Центрирование по горизонтали */
  align-items: center; /* Центрирование по вертикали */
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 500px;
}

/* Базовый стиль для строки */
.fade-out-row {
  transition: background-color 0.5s ease, opacity 0.5s ease;
  background-color: transparent;
  opacity: 1;
}

/* Класс для изменения фона на красный */
.highlight-red {
  background-color: red !important;
}

/* Класс для исчезновения (opacity) */
.fade-out {
  opacity: 0;
}

/* Скрываем элемент после завершения анимации */
.hidden {
  display: none;
}

/* Анимация появления */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Анимация покачивания */
@keyframes shake {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-10px);
  }
  50% {
    transform: translateX(10px);
  }
  75% {
    transform: translateX(-10px);
  }
  100% {
    transform: translateX(0);
  }
}
