* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App-header {
  padding: 20px;
  color: rgb(0, 0, 0);
  text-align: center;
}

img {
  max-width: 100px;
}
.displayoff {
  display: none;
}

.displayon {
  display: '';
}

/* Затемнение фона при отображении модального окна */
.modal-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
  display: none;
}
.modal-background-loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}

.modal-background-loader img {
  pointer-events: auto;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}
.textLoader {
  font-size: 26px;
  font-weight: 600;
  color: #66ABFF;
}

/* Скрываем стрелки у input[type="number"] */
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield; /* Для Firefox */
}